body
{
    margin: 0;
	padding: 0;
	background-color: #ffffff;
}

p{color: white;}

.title
{
    font-size: 2em; 
    p{color: white;}
}

.top-page
{
    height: 100%;  
    width: 100%; 
    position: relative;
    background-color: #2fff05;
    top: 0;
    left: 0;
    padding: 60px;
    z-index: -1;
    transform:rotate(-1deg);
}

.strip
{
    position: absolute;
    height: 75%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #000000;
    padding: 10px;
    transform:rotate(1deg);
}
  
/* ================================================================================================ */

  .close {
    position: absolute;
    right: 32px;
    top: 32px;
    width: 32px;
    height: 32px;
    opacity: 0.5;
  }
  .close:hover {
    opacity: 1;
  }
  .close:before, .close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 33px;
    width: 2px;
    background-color: rgb(137, 255, 27);
  }
  .close:before {
    transform: rotate(45deg);
  }
  .close:after {
    transform: rotate(-45deg);
  } 

/* ================================================================================================ */

/* 3D Image Slider */

.img-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.img-size
{
  height: auto;
  width: 100%;
}

.button {
  margin-left: 0 3%;
  width: 2rem;
  cursor: pointer;
}

.button--inactive {
  opacity: 0.2;
}

.button img {
  width: 60%;
}

.cards-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cards-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25rem;
}

.box {
/*     margin: -1.5rem; */
  width: 12rem;
  height: 20rem;
  box-shadow: 0px 0px 2rem 0px #888888;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  /* transition: 1s all; */
}

.box:nth-child(2n) {
  transform: scale(0.85);
  z-index: -1;
}

.box:nth-child(2) {
left: 5%;
}

.box:nth-child(4) {
left: -5%;
}

.box:nth-child(4n + 1) {
  transform: scale(0.75);
  z-index: -2;
}

.box:nth-child(1) {
left: 15%;
}

.box:nth-child(5) {
left: -15%;
}

.card-text-content {
  text-align: center;
  width: 75%;
}

.card-title {
  padding: 1rem;
}

.box--hide {
  display: none;
}

.move-out-from-left {
  animation: moveOutLeft 0.5s ease-in-out;
}

.move-out-from-right {
  animation: moveOutRight 0.5s ease-in-out;
}

.move-to-position5-from-left {
  animation: moveToP5Left 0.5s ease-in-out;
}

.move-to-position4-from-left {
  animation: moveToP4Left 0.5s ease-in-out;
}

.move-to-position3-from-left {
  animation: moveToP3Left 0.5s ease-in-out;
}

.move-to-position2-from-left {
  animation: moveToP2Left 0.5s ease-in-out;
}


.move-to-position1-from-left{
  animation: moveToP1Left 0.5s ease-in-out;
}

.move-to-position5-from-right{
  animation: moveToP5Right 0.5s ease-in-out;
}
.move-to-position4-from-right{
  animation: moveToP4Right 0.5s ease-in-out;
}
.move-to-position3-from-right{
  animation: moveToP3Right 0.5s ease-in-out;
}
.move-to-position2-from-right{
  animation: moveToP2Right 0.5s ease-in-out;
}
.move-to-position1-from-right{
  animation: moveToP1Right 0.5s ease-in-out;
}

@keyframes moveOutLeft {
  0% {
      transform: scale(0.75) translateX(0%);
      opacity: 1;
  }
  50% {
      transform: scale(0.5) translateX(-150%);
      opacity: 0.5;
  }
  100% {
      transform: scale(0.25) translateX(0%);
      opacity: 0;
  }
}